.hero {
    height: 600px;
    overflow: hidden;
    margin: 0 0 20px 0;
    position: relative
}

.hero .text {
    position: absolute;
    top:20px;
    left:0;
    z-index: 1;


    -webkit-transition: 1s cubic-bezier(1, -1, 0, 2);


    -o-transition: 1s cubic-bezier(1, -1, 0, 2);


    transition: 1s cubic-bezier(1, -1, 0, 2);
    -webkit-transform: translateY(75px);
        -ms-transform: translateY(75px);
            transform: translateY(75px);
    opacity: 0;
}


.hero.ready .text {
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}

.hero img {
    opacity:0;
    -webkit-transition: 1s cubic-bezier(1, -1, 0, 2);
    -o-transition: 1s cubic-bezier(1, -1, 0, 2);
    transition: 1s cubic-bezier(1, -1, 0, 2);
    -webkit-clip-path: inset(0 0 0 0);
            clip-path: inset(0 0 0 0);

}

.hero img.loaded {
    opacity: 1;
}

.hero img.masked {
    -webkit-clip-path: inset(5% 4% 45% 33%);
            clip-path: inset(5% 4% 45% 33%);    
}



